.tooltip {
	opacity: 0;
    position: absolute;
    transform: translateX(30px) translateY(73px);
	z-index: 1;
    width: 300px;
	background-color: white;
	color: black;
	text-align: center;
	padding: 7px 3px;
    border: 2px solid #E31B6D;
    /* pointer-events: none; */
    transition: all 0.1s ease-out;
    font-family: 'Montserrat', sans-serif;
}
.tooltip:hover {
    background-color: #E31B6D;
    color: white;
}

.projects a:hover .tooltip {
    opacity: 1;
}
@media (max-width: 768px) {
	.tooltip {
        width: 260px;
        transform: translateX(12px) translateY(50px);
    }
}